import React, { useEffect } from "react";
import CountUp from "react-countup";
import AOS from "aos";
import {Link} from "react-scroll";
import "aos/dist/aos.css";
function MercuryOV() {
  useEffect(() => {
    AOS.init({
      offset: 200,
      duration: 600,
      delay: 300,
    });
  }, []);
  return (
    <>
      <div className="planet-overview" data-aos="fade-up">
        <div className="overview-txt">
          <div className="planet">
            <h1>Merkuri</h1>
            <p>
              Gn sistemindki n kiik v Gn n yaxn planet olan Merkuri
              Yerin Ayndan az frql bykdr.Merkurinin sthindn Gn Yerdn
              grndyndn  qat daha byk grnr v gn i yeddi qat
              daha parlaq olur.Gn yaxn olmasna baxmayaraq, Merkuri gn
              sistemimizdki n isti planet deyil
            </p>
          </div>
          <div className="planet-details">
          <Link   to="over-mercury" smooth={true} duration={500}>   <button  className="planet-info">mumi Mlumat  <span></span><span></span><span></span><span></span></button></Link>
          <Link   to="form-mercury" smooth={true} duration={500} > <button className="planet-info">Formalamas  <span></span><span></span><span></span><span></span></button></Link>
          <Link  to="struc-mercury" smooth={true} duration={500} > <button className="planet-info"> Daxili Quruluu  <span></span><span></span><span></span><span></span></button></Link>
          </div>
        </div>
        <div className="image-png">
          <div className="iframe-planet">
            <iframe
              title="mercuryr"
              src="https://solarsystem.nasa.gov/gltf_embed/2369"
              width="100%"
              height="100%"
              frameBorder="0"
              className="mercury-ifr"
            />
          </div>
        </div>
      </div>
      <div className="container">
        <div className="planet-numbers ">
          <div className="planet-number">
            <small> Frlanma Zaman</small>
            <h1>
              <CountUp end={58} /> gn
            </h1>
          </div>
          <div className="planet-number">
            <small> Dvr etm zaman</small>
            <h1>
              <CountUp end={88} /> gn
            </h1>
          </div>
          <div className="planet-number">
            <small> Gndn olan msaf</small>
            <h1>
              <CountUp end={43.342} start={0.01} decimals={3} />M km
            </h1>
          </div>
          <div className="planet-number">
            <small> Orta temp.</small>
            <h1>
              <CountUp end={178} />
              C
            </h1>
          </div>
        </div>
      </div>
    </>
  );
}

export default MercuryOV;
