import React, { useEffect } from "react";
import CountUp from 'react-countup';
import {Link} from "react-scroll";
import AOS from "aos";
import "aos/dist/aos.css";
function MarsOV() {
  useEffect(() => {
    AOS.init({
      offset: 200,
      duration: 600,
      delay: 300,
    });
  }, []);
  return (
    <>
      <div className="planet-overview" data-aos="fade-up">
        <div className="overview-txt">
          <div className="planet">
            <h1>Mars</h1>
            <p>
              Mars Gndn gln drdnc planetdir - tozlu, soyuq, ox nazik
              bir atmosfer sahib shra dnyasdr. Mars hm d mvsmlri, qtb
              buzlaqlar, kanyonlar, snm vulkanlar v kemid daha da
              aktiv olduunu sbut edn dinamik bir planetdir.Mars, Gn
              sistemimizd n ox aradrlm cisimlrdn biridir v yad
              planetd gzmk n roverlr gndrdiyimiz yegan planetdir.
            </p>
          </div>
          <div className="planet-details">
          <Link   to="over-mars" smooth={true} duration={500}>   <button  className="planet-info">mumi Mlumat  <span></span><span></span><span></span><span></span></button></Link>
          <Link   to="form-mars" smooth={true} duration={500} > <button className="planet-info">Formalamas  <span></span><span></span><span></span><span></span></button></Link>
          <Link  to="struc-mars" smooth={true} duration={500} > <button className="planet-info"> Daxili Quruluu  <span></span><span></span><span></span><span></span></button></Link>
          </div>
        </div>
        <div className="image-png">
          <div className="iframe-planet">
            <iframe title="mars"
              src="https://solarsystem.nasa.gov/gltf_embed/2372"
              width="100%"
              height="100%"
              frameBorder="0"
              className="mercury-ifr"
            />
          </div>
        </div>
      </div>
      <div className="container">
        <div className="planet-numbers ">
          <div className="planet-number">
            <small> Frlanma Zaman</small>
            <h1><CountUp end={1}  /> gn</h1>
          </div>
          <div className="planet-number">
            <small> Dvr etm zaman</small>
            <h1><CountUp end={687}  /> gn</h1>
          </div>
          <div className="planet-number">
            <small> Gndn olan msaf</small>
            <h1><CountUp end={154.411}  decimals={3}/>M km</h1>
          </div>
          <div className="planet-number">
            <small> Orta temp.</small>
            <h1><CountUp end={13} />C</h1>
          </div>
        </div>
      </div>
    </>
  );
}

export default MarsOV;
