import React, { useEffect } from "react";
import CountUp from 'react-countup';
import {Link} from "react-scroll";
import AOS from "aos";
import "aos/dist/aos.css";
function NeptuneOV() {
  useEffect(() => {
    AOS.init({
      offset: 200,
      duration: 600,
      delay: 300,
    });
  }, []);
  return (
    <>
      <div className="planet-overview" data-aos="fade-up">
        <div className="overview-txt">
          <div className="planet">
            <h1>Neptun</h1>
            <p>
              Qaranlq, soyuq v ssdn srtli klklr trfindn rplm
              buz nhngi Neptun gn sisteminin skkizinci,n uzaq
              planetidir.Gndn Yerdn 30 qat daha ox msafd olan Neptun,
              gn sistemind gzl grnmyn yegan planetdir v kfindn
              vvl riyaziyyatlar trfindn proqnozladrlan ilk planetdir.
              2011-ci ild Neptun 1846-c ild kf edildiyi gndn bri ilk 165
              illik orbitini tamamlad.
            </p>
          </div>
          <div className="planet-details">
          <Link   to="over-neptune" smooth={true} duration={500}>   <button  className="planet-info">mumi Mlumat  <span></span><span></span><span></span><span></span></button></Link>
          <Link  to="form-neptune" smooth={true} duration={500} > <button className="planet-info">Formalamas  <span></span><span></span><span></span><span></span></button></Link>
          <Link   to="struc-neptune" smooth={true} duration={500} > <button className="planet-info"> Daxili Quruluu  <span></span><span></span><span></span><span></span></button></Link>
          </div>
        </div>
        <div className="image-png">
          <div className="iframe-planet">
            <iframe title="neptune"
              src="https://solarsystem.nasa.gov/gltf_embed/2364"
              width="100%"
              height="100%"
              frameBorder="0"
              className="mercury-ifr"
            />
          </div>
        </div>
      </div>
      <div className="container">
        <div className="planet-numbers ">
          <div className="planet-number">
            <small> Frlanma Zaman</small>
            <h1><CountUp end={0.16} start={0.01} decimals={1}/> gn</h1>
          </div>
          <div className="planet-number">
            <small> Dvr etm zaman</small>
            <h1><CountUp end={165}  /> gn</h1>
          </div>
          <div className="planet-number">
            <small> Gndn olan msaf</small>
            <h1><CountUp end={4.5 } decimals={1}  />B km</h1>
          </div>
          <div className="planet-number">
            <small> Orta temp.</small>
            <h1><CountUp end={-200} />C</h1>
          </div>
        </div>
      </div>
    </>
  );
}

export default NeptuneOV;
