import React, { useEffect } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
function UranStruc() {
  useEffect(() => {
    AOS.init({
      offset: 200,
      duration: 600,
      delay: 300,
    });
  }, []);
  return (
    <div id="struc-uranus">
      <div className="container" data-aos="fade-up">
        <div className="section-heading">
          <h2> Daxili Quruluu </h2>
        </div>
        <div className="over-paragraph">
          <p>
            Urann ktlsi 14,5 Yer ktlsi qdrdir v 4 nhng planet arasnda
            n az ktlli planetdir. Onun diametri Neptunun diametrindn azca
            byk, Yerin diametrindn is 4 df bykdr. 1,27 q/sm3 olan
            younluu il Saturndan sonra n az younlua sahib planetdir. Bu
            qiymt onu gstrir ki, planet su, metan v ammonyak kimi mxtlif
            buzlarndan tkil olunmudur.
          </p>
          <p>
            Urann strukturunun standart modelin sasn, o  tbqdn meydana
            glir: mrkzd qayal (silikat/dmir-nikel) nv, ortada buzlu
            mantiya v qaz halnda olan helium/hidrogen xarici tbq. Nv 0,55
            Yer ktlsin sahib v radiusu Urann radiusunun 20%-dn azdr.
            Mantiyasnn ktlsi 13,4 Yer ktlsi qdrdir. st atmosferi 0,5
            Yer ktlsi qdrdir v Urann radiusunun 20%-n qdr genilnir.
          </p>
          <p>
            <h3>Atmosferi </h3>
            Urann daxili hisslrinin yax tyin edilmi brk sthi olmamasna
            rmn, Urann msafdn zondlamaa lverili olan n xaricdki
            qazvari tbqsi atmosfer adlandrlr. Msafdn zondlamaq
            lverililiyi 1 bar (100 kPa) sviyysinin txminn 300 km- qdr,
            mvafiq olaraq, 100 barlq (10MPa) tzyiq v 320 K (47 C; 116 F)
            temperatura qdr uzanr.1 bar tzyiq sviyysi altnda olan
            inc termosfer, nominal sthdn iki planet radiusu qdr genilnir.
          </p>
        </div>
      </div>
      <div className="container">
        <div className="nasa-planets">
          <div className="nasa-planet">
            <img
              src="https://www.nasa.gov/sites/default/files/thumbnails/image/stsci-h-p1807a-f-3200x4000.png"
              alt="over"
            />
          </div>
          <div className="nasa-planet">
            <img
              src="https://upload.wikimedia.org/wikipedia/commons/thumb/0/0e/Uranus_clouds.jpg/220px-Uranus_clouds.jpg"
              alt="over"
            />
          </div>
          <div className="nasa-planet">
            <img
              src="https://media.visualstories.com/uploads/images/1/9219318/9219318-3314653-696_uranus_p.png"
              alt="over"
            />
          </div>
        </div>
      </div>
    </div>
  );
}

export default UranStruc;
