import React, { useEffect } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
function TheSun() {
  useEffect(() => {
    AOS.init({
      offset: 200,
      duration: 600,
      delay: 300,
    });
  }, []);
  return (<div id="over-sun">
    <div className="the-sun" data-aos="fade-up">
      <div className="container">
        <div className="section-heading">
          <h2> Gn </h2>
        </div>
        <div className="over-paragraph "> 
          <p>
            Gn sistemin ktlsinin 99,8% -ni tkil edn gn sistemimizdki
            n byk obyektdir.Gn gn sistemimizin mrkzind yerlir v
            dnya ondan 93 milyon mil uzaqlqda dvr edir.Ktlvi olsa da, Gn
            hl digr ulduz nvlri qdr byk deyil. Sar crtdan ulduz kimi
            tsnif edilir.Gnin maqnit sahsi gn klyi vasitsil Gn
            sistemin yaylr.
          </p>
          <p>
            <h3> Gnin tarixi </h3>
            Gnin ulduz inkiafnn kompter modelldirmsi v nv
            kosmoxronologiyas metodlarndan[8] istifad olunaraq Ba
            ardcllqda hesablanan yann 4,57 milyard il olduu dnlr.
            Gnin hidrogen molekulyar buludun kmsi il nc nsil T Tauri
            ulduzu kimi meydana gldiyi dnlr. Bu yeni yaranan ulduzun Bizim
            Qalaktikann mrkzindn 25-28 min iq ili uzaqlqdak demk olar
            ki, dairvi orbitin daxil olduu ehtimal olunur.
          </p>
          <p>
            <h3>Daxili quruluu </h3>
            Gn, hazrda txminn 70% hidrogen v
            ktl% 28 helyumdur ("metal" lar)% 2-dn azdr. Gn hidrogen
            nvsind helium halna gtirnd bu zamanla yavaca dyiir.
            1968-ci ild belikal alim Litium, Berillium v Borun pay
            nisbtinin vvl dnln miqdardan daha ox olduunu
            akarlamdr. 2005-ci ild  alim Neon miqdarnn vvl dnln
            miqdardan daha ox olduunu helioseysmik mahidlr saslanaraq
            iddia etmidir . 1986-c il qdr Gnin helium trkibinin Y=0,25
            olduu dncsi geni ktl trfindn qbul olunmudu, ancaq bu
            tarixd iki alim Y=0,279 miqdarnn daha doru olduunu iddia
            etmidir. 1970-ci illrd bir ox elmi tdqiqatda diqqt Gndki
            metal elementlrinin nisbtn ox olmasna cmlndi. Tk ionlu metal
            elementlrinin gf dyrlri ilk df 1962-ci ild kf olundu v
            dqiqldirilmi f dyrlri 1976-c ild hesabland.[50] Kobalt v
            Manqan kimi bzi metal elementlrinin miqdar tdqiqatlar ox inc
            qurulua sahib olmalar sbbindn tindir.
          </p> 
        </div>
        </div>
        
        
      </div>
    </div>
  );
}

export default TheSun;
