import React from 'react'
import clip from '../Videos/solar.mp4'; 
import {Link} from "react-scroll";
import AOS from "aos";
import 'aos/dist/aos.css';
import {useEffect} from 'react';
function HomeHeader() {
    useEffect(() => {
        AOS.init({
          offset: 200,
          duration: 600, 
          delay: 300,
        });
      }, []);
    return (
        <div>
             <div> 
    <video autoPlay loop={true}  muted  id="background-video">
      <source src={clip} type='video/mp4' />   
      <source src={clip} type="video/webm"/>
      <source src={clip} type="video/ogg" /> Your browser does not support the video tag. 
    </video>
        </div>
      <div className="header"   data-aos="fade-up"   >
        <div className="header-txt">
          <h1> Gn Sistemi </h1>
          <p>
            Gn Sistemi Gn v onun cazib qvvsinin tsirind olan skkiz
            planetl onlarn mlum 166 tbii peyki, be kiik planet v
            milyardlarla kiik gy cisimlrindn ibartdir. Gy cisimlri
            qrupuna asteroidlr, Koyper qurandak cisimlr, kometlr,
            meteorlar v planetlraras mhit-qaz v toz daxildir.
          </p>
        </div>
        <div className="details" >
        <Link   to="over-solar" smooth={true} duration={500}> <button className="link-info">mumi Mlumat <span></span><span></span><span></span><span></span></button></Link>
        <Link   to="over-cosmos" smooth={true} duration={500}>  <button className="link-info"> Kosmosda Yaam axtar <span></span><span></span><span></span><span></span></button></Link>
        <Link   to="over-sun" smooth={true} duration={500}> <button className="link-info">Gn <span></span><span></span><span></span><span></span></button></Link>
        </div>
      </div> 
        </div>
    )
}

export default HomeHeader
