import React, { useEffect } from "react";
import CountUp from 'react-countup';
import {Link} from "react-scroll";
import AOS from "aos";
import "aos/dist/aos.css";
function SaturnOV() {
  useEffect(() => {
    AOS.init({
      offset: 200,
      duration: 600,
      delay: 300,
    });
  }, []);
  return (
    <>
      <div className="planet-overview" data-aos="fade-up">
        <div className="overview-txt">
          <div className="planet">
            <h1>Saturn</h1>
            <p>
              Saturn gndn altnc planet v gn sistemimizin ikinci n
              byk planetidir. Minlrl gzl ringlet il bzdilmi Saturn
              planetlrin arasnda misilsizdir. Buz v qaya paralar
              halqalarna sahib olan tk planet deyil, lakin he biri Saturn
              kimi mhtm v ya mrkkb deyil.Yupiter kimi, Saturn da sasn
              hidrogen v helyumdan yaranm nhng bir topdur.
            </p>
          </div>
          <div className="planet-details">
          <Link   to="over-saturn" smooth={true} duration={500}>   <button  className="planet-info">mumi Mlumat  <span></span><span></span><span></span><span></span></button></Link>
          <Link   to="form-saturn" smooth={true} duration={500} > <button className="planet-info">Formalamas  <span></span><span></span><span></span><span></span></button></Link>
          <Link  to="struc-saturn" smooth={true} duration={500} > <button className="planet-info"> Daxili Quruluu  <span></span><span></span><span></span><span></span></button></Link>
          </div>
        </div>
        <div className="image-png">
          <div className="iframe-planet">
            <iframe title="saturn"
              src="https://solarsystem.nasa.gov/gltf_embed/2355"
              width="100%"
              height="100%"
              frameBorder="0"
              className="mercury-ifr"
            />
          </div>
        </div>
      </div>
      <div className="container">
        <div className="planet-numbers ">
          <div className="planet-number">
            <small> Frlanma Zaman</small>
            <h1><CountUp end={0.10} decimals={2}  /> gn</h1> 
          </div>
          <div className="planet-number">
            <small> Dvr etm zaman</small>
            <h1><CountUp end={29.4} decimals={1} /> il</h1>
          </div>
          <div className="planet-number">
            <small> Gndn olan msaf</small>
            <h1><CountUp end={1.4}  />B km</h1>
          </div>
          <div className="planet-number">
            <small> Orta temp.</small>
            <h1><CountUp end={-176} />C</h1>
          </div>
        </div>
      </div>
    </>
  );
}

export default SaturnOV;
