import React, { useEffect } from "react";
import CountUp from "react-countup";
import {Link} from "react-scroll";
import AOS from "aos";
import "aos/dist/aos.css";
function JupiterOV() {
  useEffect(() => {
    AOS.init({
      offset: 200,
      duration: 600,
      delay: 300,
    });
  }, []);
  return (
    <>
      <div className="planet-overview" data-aos="fade-up">
        <div className="overview-txt">
          <div className="planet">
            <h1>Yupiter</h1>
            <p>
              Gndn beinci srada olan Yupiter, gn sistemindki n byk
              planetdir - digr planetlrin birlmsindn iki qat daha
              oxdur.Yupiterin tan zolaqlar v qvrmlar slind hidrogen v
              helium atmosferind zn soyuq, klkli ammonyak v su
              buludlardr. Yupiterin simvolu olan Byk Qrmz Lk yz
              illrdir davam edn Dnyadan daha byk bir frtnadr.
            </p>
          </div>
          <div className="planet-details">
          <Link   to="over-jupiter" smooth={true} duration={500}>   <button  className="planet-info">mumi Mlumat  <span></span><span></span><span></span><span></span></button></Link>
          <Link  to="form-jupiter" smooth={true} duration={500} > <button className="planet-info">Formalamas  <span></span><span></span><span></span><span></span></button></Link>
          <Link   to="struc-jupiter" smooth={true} duration={500} > <button className="planet-info"> Daxili Quruluu  <span></span><span></span><span></span><span></span></button></Link>
          </div>
        </div>
        <div className="image-png">
          <div className="iframe-planet">
            <iframe title="jupiter"
              src="https://solarsystem.nasa.gov/gltf_embed/2375"
              width="100%"
              height="100%"
              frameBorder="0"
              className="mercury-ifr"
            />
          </div>
        </div>
      </div>
      <div className="container">
        <div className="planet-numbers ">
          <div className="planet-number">
            <small> Frlanma Zaman</small>
            <h1><CountUp end={0.9} start={0.01} decimals={1}/> gn</h1>
          </div>
          <div className="planet-number">
            <small> Dvr etm zaman</small>
            <h1><CountUp end={11}  /> gn</h1>
          </div>
          <div className="planet-number">
            <small> Gndn olan msaf</small>
            <h1><CountUp end={468.898}  decimals={3}/>M km</h1>
          </div>
          <div className="planet-number">
            <small> Orta temp.</small>
            <h1><CountUp end={-150} />C</h1>
          </div>
        </div>
      </div>
    </>
  );
}

export default JupiterOV;
