import React, { useEffect } from "react";
import CountUp from 'react-countup';
import {Link} from "react-scroll";
import AOS from "aos";
import "aos/dist/aos.css";
function VenusOV() {
  useEffect(() => {
    AOS.init({
      offset: 200,
      duration: 600,
      delay: 300,
    });
  }, []);
  return (
    <>
      <div className="planet-overview" data-aos="fade-up">
        <div className="overview-txt">
          <div className="planet">
            <h1>Venera</h1>
            <p>
              Venera, Gndn gln ikinci planetdir v dnyann n yaxn
              planet qonusudur.Venera Drd daxili, quru (v ya qayal)
              planetlrdn biridir v ls v sxl baxmndan oxar olduu
              n bzn Yerin kizi adlandrlr.Venerann sthind hava
              tzyiqi sarsdcdr - Yer krsindn 90 qat daha ox . Yer znd
              okeann bir mil altnda qarlaacaq tzyiq bnzyir.
            </p>
          </div>
          <div className="planet-details">
          <Link   to="over-venus" smooth={true} duration={500}>   <button  className="planet-info">mumi Mlumat  <span></span><span></span><span></span><span></span></button></Link>
          <Link   to="form-venus" smooth={true} duration={500} > <button className="planet-info">Formalamas  <span></span><span></span><span></span><span></span></button></Link>
          <Link  to="struc-venus" smooth={true} duration={500} > <button className="planet-info"> Daxili Quruluu  <span></span><span></span><span></span><span></span></button></Link>
          </div>
        </div>
        <div className="image-png">
          <div className="iframe-planet">
            <iframe
            title="venus"
              src="https://solarsystem.nasa.gov/gltf_embed/2343"
              width="100%"
              height="100%"
              frameBorder="0"
              className="mercury-ifr"
            />
          </div>
        </div>
      </div>
      <div className="container">
        <div className="planet-numbers ">
          <div className="planet-number">
            <small> Frlanma Zaman</small>
            <h1><CountUp end={116}    /> gn</h1>
          </div>
          <div className="planet-number">
            <small> Dvr etm zaman</small>
            <h1><CountUp end={225}  /> gn</h1>
          </div>
          <div className="planet-number">
            <small> Gndn olan msaf</small>
            <h1><CountUp end={67} />M km</h1>
          </div>
          <div className="planet-number">
            <small> Orta temp.</small>
            <h1><CountUp end={452} />C</h1>
          </div>
        </div>
      </div>
    </>
  );
}

export default VenusOV;
